
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  s-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.content-mg{
  margin-left: 10%;
  margin-right: 10%;
}
.content-pd{
  padding-left: 10%;
  padding-right: 10%;
  text-align: left;
  padding-top: 20px;
}
.logo-pic{
  float: left;
  margin-top: -6%;
  margin-right: 1rem;
}
.navbar{
  padding: 0.5rem 2rem;
  align-items: center;
  background-color:#fff !important;
  border: 1px solid #f1f1f1 !important;
  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
      background-color: #eee;
    }
  }
}
.mg-auto{
  position: relative;
  left: auto !important;
}
.navbar-brand{
  font-weight: bold;
  color: #2c3e50;
}
.ml-auto{
  float: right !important;
}
.rounded-circle{
  border-radius: 50%;
}
.mr-2{
  margin-right: 0.66rem;
}
.page-bottom{
  width: 100%;
  height: 50px;
}
